
body {
  margin: 0;
  font-family: futura-pt,sans-serif;
}

code {
  font-family: 'Courier New', Courier, monospace;
}
/*Make my search input like the instagram input design*/
 


  .search-container {
    display: flex;
    align-items: center;
    background-color: #E6EBF5;
    border-radius: 5px;
    padding: 5px 10px;
    width: 100%;
    max-width: 500px;
    
  }
  
  .search-input {
    border: none;
    outline: none;
    background-color: transparent;
    flex: 1;
    margin-left: 30px;
    font-size: 16px;
    font-weight: 500;
  
  }
  
  .search-icon {
    color: #9FA4B9;

  }
  