.container {
  width: 100%;

  margin: auto;
  background-color: #ffffff;
  padding: 0px 0px 0px 0px;
  overflow-y: hidden;
  border: 1px solid #efefef;
}

.header {
  height: 58px;
  border-bottom: 1px solid #efefef;
}

.header_body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.img_container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 13px;
  margin-left: 25px;
}

.img_container img {
  height: 38px;
  width: 38px;
  border-radius: 50%;
}

.img_container h5 {
  margin-left: 16px;
  margin-top: 5px;
}

.call_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 29px;
  margin-right: 20px;
  margin-top: 5px;
}

.conversations {
  overflow-y: auto;
  height: calc(100vh - 162px);
}

.sender {
  padding: 10px 20px;
  background-color: #2ABF00;  
  float: right;
  text-align: right;
  color: #ffffff;
  border-radius: 10px;
  margin: 5px 0px;
}

.receiver {
  padding: 10px 20px;
  background-color: #efefef;
  float: left;
  text-align: left;
  border-radius: 10px;
  margin: 5px 0px;
}


.senderImg {
  display: block;
  float: right;
  margin: 5px 0px ;
}

.receiverImg {
  display: block;
  float: left;
  margin: 5px 0px ;
}



.imageFrame {
  display: block;
  margin: auto;
  width: 200px;
  height: 200px;
}

.imageFrame img {
  width: 180px;
  height: 180px;
  display: block;
  margin: auto;
}

.footer {
  height: 100px;
  border-top: 1px solid #F0EFF4;
  display: flex;
  align-items: center;
}

.footer_item {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.input_div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 328px;
  height: 45px;

}

.footer_item input {

  width: 100%;
  height: 100%;
  border-radius: 4px;
  font-family: Arial, Helvetica, sans-serif;
  outline: none;
  padding-left: 15px;
}

.imgIcon{
  position: relative;
}

.cameraIcon {
  z-index: 999;
  display: inline-block;
}

.imgUp {
  position: absolute;
  top: 0;
  left: 1;
  z-index: 0;
  color: transparent;
 
  border: none !important;
}




.call {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100vh;
  width: 40%;
  z-index: 999;
  background: #ffffff;
  padding-top: 200px;
}
